html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-family: $font-main;
  color: $text-dark;
  background-color: $text-light;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
