html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: inherit; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  background-color: #f9fbfd; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

.container {
  max-width: 1220px;
  padding: 0 30px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      max-width: 678px;
      padding: 0; } }
  @media (max-width: 767px) {
    .container {
      max-width: auto;
      padding: 0 15px; } }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 109px;
  color: #f9fbfd; }

.header__logo-link {
  display: flex;
  align-items: center;
  transition: 0.3s; }
  .header__logo-link:hover {
    opacity: 0.6; }
  .header__logo-link:focus {
    outline: dashed 2px rgba(255, 255, 255, 0.2);
    outline-offset: 0; }
  .header__logo-link:active {
    opacity: 0.3; }

.header__logo-svg {
  fill: #f9fbfd; }

.header__toggle {
  display: none; }

@media (max-width: 1023px) {
  .header {
    min-height: 83px; }
  .header__logo-svg {
    fill: #f9fbfd;
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .header {
    position: relative;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    min-height: 61px;
    max-width: none; }
  .header__logo-link {
    padding-left: 15px;
    padding-top: 20px;
    position: absolute;
    z-index: 5; }
  .header__logo-svg {
    width: 42px;
    height: 22px; }
  .header__toggle {
    position: absolute;
    top: 25px;
    right: 15px;
    display: block;
    width: 24px;
    height: 14px;
    background-image: url("../img/svg/burger.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    border: none; }
  .header--opened .header__toggle {
    position: absolute;
    top: 25px;
    right: 15px;
    width: 13px;
    height: 13px;
    padding: 0;
    z-index: 5;
    background-image: url("../img/svg/cross.svg"); }
  .header--nojs .header__toggle {
    display: none; }
  .header--nojs .header__nav-list {
    position: relative;
    display: flex; }
  .header--nojs .header__logo-svg {
    fill: #011c40; }
  .header--opened .header__nav-list {
    position: absolute;
    display: flex;
    top: 0; }
  .header--opened .header__logo-svg {
    fill: #011c40; }
  .header--closed .header__nav-list {
    display: none; } }

.main-nav {
  width: 80%; }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  min-height: 38px;
  font-size: 16px;
  line-height: 20px;
  list-style: none;
  margin: 0;
  padding: 0;
  font-weight: 500; }

.main-nav__link {
  padding: 10px 0 20px 36px;
  text-align: right;
  transition: 0.3s; }
  .main-nav__link:hover {
    opacity: 0.7;
    text-decoration: underline; }
  .main-nav__link:focus {
    outline: dashed 2px rgba(255, 255, 255, 0.2);
    outline-offset: 0; }
  .main-nav__link:active {
    opacity: 0.3; }

@media (max-width: 1023px) {
  .main-nav__list {
    font-size: 14px;
    line-height: 17px; }
  .main-nav__link {
    padding-left: 26px;
    text-align: right; } }

@media (max-width: 767px) {
  .main-nav {
    position: relative;
    display: flex;
    justify-content: flex-end;
    width: 100%; }
  .main-nav__list {
    position: absolute;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
    min-height: 299px;
    padding: 77px 15px 15px;
    font-size: 18px;
    line-height: 22px;
    font-weight: 500;
    color: #011c40;
    background-color: #f9fbfd; }
  .main-nav__item--destinations {
    order: -1; }
  .main-nav__link {
    display: block;
    padding: 11px 0 10px; } }

.page-main {
  margin-top: -109px; }

.page-main__promo {
  min-height: 650px;
  color: #f9fbfd;
  background-color: #2f80ce;
  background-image: url("../img/page-main-bg-desktop@1x.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; }
  @media (min-resolution: 144dpi), (min-resolution: 2dppx) {
    .page-main__promo {
      background-image: url("../img/page-main-bg-desktop@2x.jpg"); } }

.page-main__promo h1 {
  padding: 270px 0 0 97px;
  margin: 0;
  font-size: 54px;
  line-height: 66px;
  font-weight: 700; }

.page-main__promo p {
  padding: 12px 0 0 97px;
  margin: 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400; }

@media (max-width: 1023px) {
  .page-main {
    margin-top: -83px; }
  .page-main__promo {
    min-height: 638px;
    background-image: url("../img/page-main-bg-tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 2dppx) {
    .page-main__promo {
      background-image: url("../img/page-main-bg-tablet@2x.jpg"); } }

@media (max-width: 1023px) {
  .page-main__promo h1 {
    padding-left: 0;
    font-size: 50px;
    line-height: 61px; }
  .page-main__promo p {
    padding-left: 0;
    font-size: 18px;
    line-height: 22px; } }

@media (max-width: 767px) {
  .page-main {
    margin-top: -61px; }
  .page-main__promo {
    min-height: 500px; }
  .page-main__promo h1 {
    padding-top: 177px;
    font-size: 36px;
    line-height: 45px; }
  .page-main__promo p {
    padding-top: 10px;
    font-size: 16px;
    line-height: 22px; } }

.footer {
  background-color: #011c40;
  color: #f9fbfd; }

.footer__inner {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 138px; }

.footer__logo-link:hover .footer__logo-svg {
  fill: #76bff1; }

.footer__logo-link:focus {
  outline: dashed 2px rgba(255, 255, 255, 0.2);
  outline-offset: 0; }

.footer__logo-link:active {
  opacity: 0.3; }

.footer__logo-svg {
  fill: #f9fbfd; }

.footer__nav-list,
.footer__social {
  display: flex;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0; }

.footer__nav-list {
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  margin-left: 22px; }

.footer__social {
  align-items: center;
  width: 117px;
  margin-left: auto; }

.footer__nav-link {
  padding: 10px 17px;
  display: block; }
  .footer__nav-link:hover {
    color: #76bff1; }
  .footer__nav-link:focus {
    outline: dashed 2px rgba(255, 255, 255, 0.2);
    outline-offset: 0; }
  .footer__nav-link:active {
    opacity: 0.5; }

.footer__social-svg {
  fill: #f9fbfd; }

.footer__social-link:hover .footer__social-svg {
  fill: #76bff1; }

.footer__social-link:focus {
  outline: dashed 2px rgba(255, 255, 255, 0.2);
  outline-offset: 3px; }

.footer__social-link:active {
  opacity: 0.3; }

@media (max-width: 1023px) {
  .footer__inner {
    min-height: 120px; }
  .footer__logo-svg {
    width: 38px;
    height: 20px; }
  .footer__nav-list {
    margin-left: 25px;
    font-size: 12px;
    line-height: 15px; }
  .footer__nav-link {
    padding: 0 24px 0 0; }
  .footer__social {
    width: 87px; }
  .footer__social-svg--facebook {
    width: 9px;
    height: 16px; }
  .footer__social-svg--instagram {
    width: 16px;
    height: 16px; }
  .footer__social-svg--twitter {
    width: 16px;
    height: 13px; } }

@media (max-width: 767px) {
  .footer__inner {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 42px;
    padding-bottom: 42px; }
  .footer__logo-svg {
    width: 42px;
    height: 22px; }
  .footer__nav-list {
    flex-direction: column;
    margin: 15px 0 17px; }
  .footer__nav-list,
  .footer__social {
    margin-left: 0; }
  .footer__nav-link {
    padding: 8px 24px 9px 0; } }
